import React from 'react';
import './returncoin.css';

class Card extends React.Component {
    constructor(props){
        super(props);
        this.state = {};
    }

    render(){
        return(
            <div className="coin-card">
                <h4 className="card-unit">USDT</h4>
                <div className={this.props.color ? 'card-item color': 'card-item'}>
                    <ul className="clearfix">
                        <li>
                            <span className="title"></span>
                            <span className="info">JB321512658458</span>
                        </li>
                        <li>
                            <span className="title"></span>
                            <span className="info">ETH</span>
                        </li>
                        <li>
                            <button className={this.props.color ? 'return-btn' : 'return-btn hide'}></button>
                        </li>
                    </ul>
                    <ul className="clearfix">
                        <li>
                            <span className="title"></span>
                            <span className="info">5000</span>
                        </li>
                        <li>
                            <span className="title"></span>
                            <span className="info">50000</span>
                        </li>
                        <li>
                            <span className="title"></span>
                            <span className="info">50000</span>
                        </li>
                    </ul>
                    <ul className="clearfix">
                        <li>
                            <span className="title"></span>
                            <span className="info">3<i></i></span>
                        </li>
                        <li>
                            <span className="title"></span>
                            <span className="info">300<i>ETH</i></span>
                        </li>
                        <li>
                            <span className="title"></span>
                            <span className="info"></span>
                        </li>
                    </ul>
                </div>
            </div>
        );
    }
}

export default Card;


